import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { PageTitle, PageBody } from '../components/PageMain';

export default function Home ({start, setFrame}) {
    const handleButton = (name) => {
        // console.log(name)
        setFrame(name)
    }
    return (
        <div id="home" className="page">
            <PageTitle title_text={"Welcome"}/>

            <PageBody>
                <section id="section-1" className="section">
                    <div className="section-heading">
                        <h2>Title</h2>
                    </div>
                    <div className="section-body">
                        <p>Welcome to your personal digital photobooth!</p>
                    </div>
                </section>
                <section id="section-1" className="section">
                    <div className="section-heading">
                        <h2>Instructions:</h2>
                    </div>
                    <div className="section-body">
                        <ol>
                            <li>This photobooth captures <strong>4 pictures</strong> in a row</li>
                            <li>You have <strong>3 seconds</strong> for each shot  no retakes!</li>
                            <li>Strike your best pose and have fun!</li>
                        </ol>

                        <p>After the session, <span style={{ color: "pink" }}>download your digital copy</span> and share the fun!</p>
                        <button onClick={() => handleButton(-1)}>Default</button>
                        <button onClick={() => handleButton('xav')}>Xavier</button>
                        <button onClick={() => handleButton('zay')}>Zayne</button>
                        <button onClick={() => handleButton('raf')}>Rafayel</button>
                        <button onClick={() => handleButton('syl')}>Sylus</button>
                        <button onClick={() => handleButton('cal')}>Caleb</button>
                    </div>
                </section>
            </PageBody>
        </div>
    );
}