import React, { useRef, useState, useEffect, useLayoutEffect, useCallback } from "react";

import 'context-filter-polyfill/dist/index.js';

import Webcam from "react-webcam"; // https://github.com/mozmorris/react-webcam

import Skeleton from 'react-loading-skeleton'
import 'react-loading-skeleton/dist/skeleton.css'

import { GetWindowContext } from '../contexts/WindowProvider';

import timer_db from '../database/timers.json';
import filter_db from '../database/filters.json';

const MAX_NUM = 4;
const TIMEOUT_LOAD = 1500;

export default function PhotoBooth ({ frameFolderName, SET_CAPTURED_IMGS }) {
    const win = GetWindowContext();

    // REF CONTAINERS
    const page_pb_container = useRef(null);         // main{ .photobooth {...} }
    const video_container = useRef(null);           // div.video-container
    const capture_container = useRef(null);         // div.capture

    const [loaded, setLoaded] = useState(false);    // div.video-container -> current != null & after timeout(1500)

    // USER CUSTOMIZATIONS -> TIMER COUNTDOWN & FILTER
    const [countdownCount, setCountdownCount] = useState(-1);   // updates countdown display
    const [timerCount, updateTimerCount] = useState(3);
    /* timer value: maximum number of seconds in-between image captures
        - accepts user input
        - values = {3, 5, 10}
        - default: 3 seconds */
    const [filter, updateFilter] = useState("none");
    /* filter value: css filter(...) applied to video feed + captures
        - accepts user input
        - default: none */ 

    // CAMERA ESSENTIAL
    const videoRef = useRef(null);
    const canvasRef = useRef(null);
    const [userMediaError, setUserMediaError] = useState(null); // react-webcam check
    const checkUserMedia = (event) => setUserMediaError(event instanceof Error);
    const CAPTURE_CONTRAINTS = {
        facingMode: "user",
        audio: false,
        width: { ideal: 1920 },  // Set to Full HD
        height: { ideal: 1080, min: 280 },
        frameRate: { ideal: 30 }, // Keep a good frame rate
        aspectRatio: parseFloat(3/2)
    };

    // IMAGES
    const [frame, loadFrame] = useState([]);                // images for frames
    const [capturing, toggleCapturing] = useState(false);  // track if photobooth is in capturing state
    const [capturedImgs, addCapturedImg] = useState([]);       // array of captures, length = [0, MAX_NUM]
    const [numImgs, incrNumImgs] = useState(0);             // track number of captured images
    const [isFlashing, setIsFlashing] = useState(false);    // track camera flash simulation

    // const [folderName, setFolderName] = useState(frameFolder);
    const [frameDirection, setFrameDirection] = useState([])
    var frames

    // const getFrame = () => {loadFrame(FrameHandler('raf'))}

    useEffect(() => {
        // scroll down to div.photobooth so it takes up entire view (100dvh)
        window.scrollTo(0, document.getElementById('pb').offsetTop);

        if(isNaN(frameFolderName)){
            // (async() => {
            //     for (let i = 1; i <= MAX_NUM; i++){
            //         const img = new Image()
            //         try{
            //             const response = await import(`../img/${frameFolderName}/${i}.png`)
            //             img.src = response.default
            //         } finally{
            //             img.onload = function(){
            //                 loadFrame((prev) => [...prev, img])
            //             }
            //         }
            //     }
            //     setTimeout(() => setLoaded(true), TIMEOUT_LOAD)
            // })();
            frames = new Array(4)
            for (let i = 1; i <= MAX_NUM; i++){
                // console.log(`../img/${frameFolderName}/${i}.png`)
                import(
                    `../img/${frameFolderName}/${i}.png`
                ).then((image) => {
                    const img = new Image()
                    img.src = image.default
                    // const url = window.URL.createObjectURL(image.default)
                    // console.log(img)
                    img.onload = function(){
                        frames[i-1] = img
                        // console.log(frames[i-1])
                        // frames.push(img)
                        // loadFrame((prev) => [...prev, img])
                    }
                });
            }

            // console.log(frames)

            loadFrame(frames)

            // frameDirection = new Array(MAX_NUM).fill(0)

            switch(frameFolderName){
                case 'xav':
                    setFrameDirection([0,1,0,1])
                    break;
                case 'zay':
                    setFrameDirection([1,1,0,1])
                    break;
                case 'raf':
                    setFrameDirection([0,0,1,0])
                    break;
                case 'syl':
                    setFrameDirection([0,1,0,0])
                    break;
                case 'cal':
                    setFrameDirection([0,1,0,1])
                    break;
                default:
                    setFrameDirection([0,0,0,0])
                    break;
            }

            setTimeout(() => setLoaded(true), TIMEOUT_LOAD)
        }
        

        // loading images for frames
        // (async() => {
            
        //     let frameList = [raf, raf, raf, raf]

        //     frameList.forEach((i) => {
        //         const img = new Image()
        //         img.src = i
        //         img.onload = function(){
        //             loadFrame((prev) => [...prev, img])
        //         }
        //         console.log(frame)
        //     })

            
        // })();

        const onVisibilityChange = () => {
            if (document.visibilityState === 'visible') {
            //   console.log("Tab reopened, refetch the data!");
            } else{
                // console.log(stream)
                // stream.getTracks()[0].stop();
            }
        };

        document.addEventListener("visibilitychange", onVisibilityChange);
        
        return () => {
            document.removeEventListener("visibilitychange", onVisibilityChange);
        };
    }, []);

    useEffect(() => {
        if (video_container.current && !loaded) setTimeout(() => setLoaded(true), TIMEOUT_LOAD);
    }, [video_container.current])

    // Countdown to take 4 pictures automatically
    const startCountdown = () => {
        if (capturing) return;
        toggleCapturing(true);
        if(capturedImgs.length > 0) addCapturedImg([])
        if(numImgs > 0) incrNumImgs(0)
    
        let numImgsTaken = 0;
        const newCapturedImages = [];
    
        const captureSequence = async () => {
            if (numImgsTaken >= MAX_NUM) {
                setCountdownCount(null);
                toggleCapturing(false);

                try {
                    // COMPONENT ENDS HERE
                    SET_CAPTURED_IMGS(newCapturedImages);
                } catch (error) {
                    console.error("Error navigating to preview:", error);
                }
                return;
            }

            let timeLeft = timerCount //timerCount;
            setCountdownCount(timeLeft);
            setIsFlashing(false);

            const timer = setInterval(() => {
                timeLeft -= 1;
                setCountdownCount(timeLeft);

                if (timeLeft <= 0) {
                    setIsFlashing(true);
                    clearInterval(timer);

                    // SCREENSHOT
                    const canvas = canvasRef.current;
                    const sc = new Image()
                    sc.src = videoRef.current.getScreenshot()
                    sc.onload = function(){
                        const context = canvas.getContext("2d");
                        // context.reset(); // context-filter-polyfill library :(

                        const targetWidth = 1200;
                        const targetHeight = 800;

                        canvas.width = targetWidth;
                        canvas.height = targetHeight;

                        // DRAW IMAGE
                        const scRatio = sc.width / sc.height;
                        const targetRatio = targetWidth / targetHeight;
                        
                        let drawWidth = sc.width;
                        let drawHeight = sc.height;
                        let startX = 0;
                        let startY = 0;

                        if (scRatio > targetRatio) {
                            drawWidth = drawHeight * targetRatio;
                            startX = (sc.width - drawWidth) / 2;
                        } else {
                            drawHeight = drawWidth / targetRatio;
                            startY = (sc.height - drawHeight) / 2;
                        }

                        context.filter = filter !== 'none' ? filter : 'none';

                        context.drawImage(
                            sc,
                            startX, startY, drawWidth, drawHeight,  
                            0, 0, targetWidth, targetHeight        
                        );

                        // DRAW FRAME

                        // console.log(numImgsTaken, frameDirection[numImgsTaken])

                        if(frame.length > 0){
                            let frameRatio = frame[numImgsTaken].width / frame[numImgsTaken].height
                            drawWidth = frame[numImgsTaken].width
                            drawHeight = frame[numImgsTaken].height

                            if (frameRatio > targetRatio) {
                                drawHeight *= targetWidth / drawWidth;
                                drawWidth = targetWidth
                            } else {
                                drawWidth *= targetHeight / drawHeight;
                                drawHeight = targetHeight
                            }

                            if(frameDirection[numImgsTaken]){
                                // console.log(frameDirection[numImgsTaken])
                                // apply on right side
                                startX = canvas.width - drawWidth
                            } else{
                                // apply on left side
                                startX = 0
                            }

                            context.drawImage(
                                frame[numImgsTaken],
                                startX, 0, drawWidth, drawHeight
                            );
                        }
                        
                        

                        const snapshot = canvas.toDataURL("image/png")

                        newCapturedImages.push(snapshot)

                        addCapturedImg((prevImages) => [...prevImages, snapshot])

                        numImgsTaken += 1;
                        incrNumImgs(numImgsTaken);
                    }
                    setTimeout(captureSequence, 1000);
                }
            }, 1000);
        }

        captureSequence();
    };
    
    return (
        <div id="pb" className={`photobooth`} ref={page_pb_container}>

            <div className={`video-container${!loaded ? ` load` : ``}`}
                ref={video_container}

                // THANK YOU: https://stackoverflow.com/a/77965501
                {...video_container.current && (win.clientWidth >= 844) ?
                    {style:{'height' : `min(calc(100dvh - 160px), ${page_pb_container.current.offsetWidth * 2/3}px`}}
                :
                    {}
                }
                
            >
                
                {!loaded && video_container.current &&
                    <div className="video-temp" style={
                        win.clientWidth < 844 ?
                            {'width': 'calc(' + video_container.current.offsetHeight + 'px * (3/2))', 'opacity' : 1}
                        :
                            {'width': 'calc((100dvh - ' + capture_container.current.offsetHeight + 'px) * (3/2))', 'opacity' : 1}
                    }>
                        <Skeleton duration={1.5} style={{'height':'100%'}}/>
                    </div>
                }
                <Webcam className="video"
                    audio={false}
                    mirrored={true}
                    ref={videoRef}
                    videoConstraints={CAPTURE_CONTRAINTS}
                    screenshotFormat="image/png"
                    style={{ filter }}
                    onUserMedia={checkUserMedia}
                    onUserMediaError={checkUserMedia}
                />

                {!userMediaError && frame.length > 0 &&
                    <img
                        // src={require(`../img/raf/${numImgs + 1 <= MAX_NUM ? numImgs + 1 : MAX_NUM}.png`)}
                        {...loaded && {src:frame[numImgs < MAX_NUM ? numImgs : MAX_NUM-1].src}}
                        
                        // {...filter !== 'none' && {style:{filter}}}

                        {...!loaded && {className:'load'}}

                        {...loaded && {style:frameDirection[numImgs < MAX_NUM ? numImgs : MAX_NUM-1] ? {right:0, filter} : {left:0, filter}}}
                    />
                }

                <canvas ref={canvasRef} className="hidden" />

                {isFlashing &&
                    <div className="flash" onAnimationEnd={() => setIsFlashing(false)} />
                }

                {!capturing && !userMediaError && numImgs != MAX_NUM &&
                    <div className="customize" aria-hidden={capturing}>
                        <div className="container">
                            <div className="timer">
                                {win.clientWidth >= 844 && win.clientHeight >= 480 ? <>
                                    <div className="heading">Timer</div>
                                    <ul>
                                        {timer_db.map((option, i) => (
                                            <li key={i}>
                                                <input type='radio' name='timer' value={option.value} id={option.id} defaultChecked={timerCount == option.value} onChange={() => updateTimerCount(option.value)}/>
                                                <label htmlFor={option.id} className='btn-secondary'>{option.label}</label>
                                            </li>
                                        ))}
                                    </ul>
                                </> : <>
                                    <label htmlFor="timer" className="heading">Timer</label>
                                    <select name="timer" id="timer" value={timerCount} onChange={event => updateTimerCount(event.target.value)} required>
                                        {timer_db.map((option, i) => (
                                            <option key={i} value={option.value}>{option.label}</option>
                                        ))}
                                    </select>
                                </>}
                            </div>
                        </div>
                        <div className="container">
                            <div className="filter">
                                {win.clientWidth >= 844 && win.clientHeight >= 480 ? <>
                                    <div className="heading">Filter</div>
                                    <ul>
                                        {filter_db.map((option, i) => (
                                            <li key={i}>
                                                <input type='radio' name='filter' id={option.id} defaultChecked={filter == option.value} onChange={() => updateFilter(option.value)}/>
                                                <label htmlFor={option.id} className='btn-secondary'>{option.label}</label>
                                            </li>
                                        ))}
                                    </ul>
                                </> : <>
                                    <label htmlFor="filter" className="heading">Filter</label>
                                    <select name="filter" id="filter" value={filter} onChange={event => updateFilter(event.target.value)} required>
                                        {filter_db.map((option, i) => (
                                            <option key={i} value={option.value}>{option.label}</option>
                                        ))}
                                    </select>
                                </>}
                            </div>
                        </div>
                    </div>
                }
            </div>
            
            <div className="capture" ref={capture_container}
                {...win.clientWidth < 844 ?
                    loaded || video_container.current ? 
                        {style:{'height': 'max(calc(100dvh - ' + page_pb_container.current.offsetWidth + 'px * (2/3)), calc((100dvh - ' + video_container.current.offsetHeight + 'px))', 'width': 'calc(' + video_container.current.offsetHeight + 'px * (3/2))'}}
                    :
                        {style:{'height':'220px'}}
                :
                    video_container.current && {style:{'width': 'min(100%, calc((100vh - 160px) * (3/2)))'}}
                }
            > {loaded &&
                <div className="container capture-1">
                    {numImgs >= 1 && <figure className="pic"><img src={capturedImgs[0]} className="size-preview"/></figure>}
                    {numImgs >= 2 && <figure className="pic"><img src={capturedImgs[1]} className="size-preview"/></figure>}
                </div>}
                {video_container.current && <div className="control">
                    <button onClick={startCountdown} disabled={!loaded || userMediaError || capturing} className={`btn-primary btn-icon${capturing ? ` capturing`:``}${userMediaError ? ` usermediaerror` : ``}`}>
                        {!loaded ?
                            <i className="fa fa-solid fa-spinner"></i>
                        :
                            userMediaError ?
                                <i className="fa fa-solid fa-ban"></i>
                            :
                                !capturing ?
                                    <i className="fa fa-solid fa-camera"></i>
                                :
                                    countdownCount ?
                                        countdownCount
                                    :
                                        <i className="fa fa-solid fa-floppy-disk"></i>
                        }
                    </button>
                </div>}
                {loaded &&<div className="container capture-2">
                    {numImgs >= 3 && <figure className="pic"><img src={capturedImgs[2]} className="size-preview"/></figure>}
                    {numImgs >= 4 && <figure className="pic"><img src={capturedImgs[3]} className="size-preview"/></figure>}
                </div>}
            </div>
        </div>
    );
};