import React, { useState, useEffect } from "react";

import { GetWindowContext } from '../contexts/WindowProvider';
import NavHeader, {getHeight} from '../components/NavHeader';
// import Footer from '../components/Footer';

// import Home from "./components/Home2";
import Home from "./Home";
import PhotoBooth from "./PhotoBooth";
import PhotoPreview from "./PhotoPreview";

export default function PageLayout({ children }){
    const win = GetWindowContext();
    // console.log(win.clientHeight, win.clientWidth)

    const [capturedImages, setCapturedImages] = useState([]);
    const [component, setComponent] = useState(null);

    const [frame, setFrame] = useState(null);

    function start() {
        // console.log("here?")
        setComponent(<PhotoBooth
            frameFolderName={frame}
            SET_CAPTURED_IMGS={setCapturedImages}
            vw={win.clientWidth}
            vh={win.clientHeight}
        />)
    }

    useEffect(() => {
        if (frame){
            // console.log(frame)
            start()
            // setTimeout(() => start(), 500);
        } 
    }, [frame])

    function home(){
        // console.log("homeeee");
        setFrame(null)
        // console.log(frame)

        setComponent(<Home start={start} setFrame={setFrame}/>)
    }

    useEffect(() => {
        // if(frame){
        // console.log("hereee")
        setFrame(null)
        home()
        // }
    }, [])

    useEffect(() => {
        // console.log("here?", capturedImages.length)
        if(capturedImages.length > 0) setTimeout(setComponent(<PhotoPreview start={start} capturedImages={capturedImages} />), 500)
    }, [capturedImages])

    return(<>
        <NavHeader home={home}/>
        <main>
            { component }
        </main>
        {/* <Footer /> */}
    </>);
}